.about_container {
	display: grid;
	grid-template-columns: 35% 50%;
	gap: 15%;
	margin-bottom: 20rem;
}
.about_me {
	width: fit-content;
	border-radius: 2rem;
	background: linear-gradient(
		45deg,
		transparent,
		var(--color-primary),
		transparent
	);
	display: grid;
	place-items: center;
}

.about_me-image {
	width: fit-content;
	border-radius: 2rem;
	overflow: hidden;

	transition: var(--transition);
}
.about_me-image:hover {
	transform: rotate(0);
}

.about_cards {
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	gap: 1.5rem;
}
.about_card {
	background: var(--color-bg-variant);
	border: 1px solid transparent;
	padding: 2rem;
	text-align: center;
	transition: var(--transition);
}

.about_card:hover {
	background: transparent;
	border-color: var(--color-bg-variant);
	cursor: default;
}
.about_icons {
	color: var(--color-primary);
	font-size: 1.4rem;
	margin-bottom: 1rem;
}
.about_card h5 {
	font-size: 0.9rem;
}
.about_card small {
	font-size: 0.7rem;
	color: var(--color-light);
}

.about_content p {
	margin: 2rem 0 2.6rem;
	color: var(--color-white);
	font-weight: 700;
}
/* //////////////////media Queries bigger screen////////// */
@media screen and (max-width: 1024px) {
	.about_container {
		grid-template-columns: 1fr;
		gap: 0;
	}
	.about_me {
		width: 50%;
		margin: 2rem auto 4rem;
	}
	.about_me-image {
		border-radius: 2rem;
	}
	.about_content p {
		margin: 1rem 0 1.5rem;
	}
}

/* ////////////media-queries small screen //////////////*/
@media screen and (max-width: 600px) {
	.about_me {
		width: 65%;
		margin: 0 auto 3rem;
	}

	.about_card {
		grid-template-columns: 1fr 1fr;
		gap: 1rem;
	}
	.about_content {
		text-align: center;
	}
	.about_content p {
		margin: 1.5rem 0;
	}
	.about_me-image {
		border-radius: 2rem;
	}
}
