@import url('https://fonts.googleapis.com/css2?family=Baloo+Chettan+2:wght@400;700&family=Open+Sans:wght@300;400;500;600;700&family=Poppins:wght@100;300;400;600;700;800;900&family=Raleway:wght@100;300;500;600;700&family=Urbanist:wght@100;200;300;400;500;600;700;900&display=swap');

*{
    margin: 0;
    padding: 0;
    border: 0;
    box-sizing: border-box;
    outline: 0;
    list-style: none;
    text-decoration: none;
    text-transform: capitalize;
}

:root{
    --color-bg: #1f1f38;
    --color-bg-variant: #2c2c6c;
    --color-primary: #4db5ff;
    --color-primary-variant: rgba(77,181,255, .4);
    --color-white: #fff;
    --color-light: rgba(255, 255, 255, .6);

    
    --container-width-lg: 75%;
    --container-width-md: 86%;
    --container-width-sm: 90%;

    --transition: all 400ms ease;
}

html{
    scroll-behavior: smooth;
     /* font-size: 62.5%;  */
}
::-webkit-scrollbar{
 display: none;
}

body{
    font-family: 'Poppins', sans-serif;
    background: var(--color-bg);
    color: var(--color-white);
    line-height: 1.7;    
}

/* //////////////////////general styles////////// */
.container{
    width: var(--container-width-lg);
    margin: 0 auto;
}

h1,h2,h3,h4,h5,h6{
    font-weight: 600;
}
h1{
    /* backdrop-filter: blur(10px); */
    font-size: 2.5rem;
}
section{
    margin-top: 8rem;
    
 
}
section > h2, section > h5{
   text-align: center;
   color: var(--color-light);
}

section > h2{
    margin-bottom: 3rem;
    color: var(--color-primary);
}
.text-light{
    color: var(--color-light);
}

section{
    margin-top: 8rem;
    
}
a{
    color: var(--color-primary);
    transition: var(--transition);
}
a:hover{
    color: var(--color-white);
}
.btn{
    width: max-content;
    display: inline-block;
    color: var(--color-primary); 
    padding: .75rem 1.2rem;
    border-radius: .4rem;
    cursor: pointer;
    border: 1px solid var(--color-primary);
    transition: var(--transition);
   
}
.btn:hover{
    background: var(--color-white);
    color: var(--color-bg);
    border-color: transparent;
}
.btn-primary{
    background: var(--color-primary);
    color: var(--color-bg);
   
}
img{
    display: block;
    width: 100%;
    object-fit: cover;
}
/* ///////medium screen///////// */
@media screen and(max-width: 1024px){
    .container{
        width: var(--container-width-md);
    }
    section{
        margin-top: 6rem;
    }
}
/* ///////small screen///////// */
@media screen and(max-width: 600px){
    .container{
        width: var(--container-width-sm);
    }
    section > h2{
        margin-bottom: 2rem;
    }
}